import React, { useEffect, useState } from 'react';
import { Link, NavLink } from 'react-router-dom';
import styles from './Navbar.module.css';

export default function Navbar() {
  const [navbarClass, setNavbarClass] = useState('');

  useEffect(() => {
    const handleScroll = () => {
      const scrollY = window.scrollY;
      if (scrollY > 30) {
        setNavbarClass(styles.smallNavbar);
      } else {
        setNavbarClass('');
      }
    };

    window.addEventListener('scroll', handleScroll);

    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  return (
    <>
      <nav className={`navbar navbar-expand-sm navbar-light bg-light fixed-top ${styles.Navcol} ${navbarClass}`}>
        <div className="container">
          <Link className={`navbar-brand text-light ${styles.frame} fw-700`} to="/">
            START FRAMEWORK
          </Link>
          <button
            className={`navbar-toggler d-lg-none`}
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#collapsibleNavId"
            aria-controls="collapsibleNavId"
            aria-expanded="false"
            aria-label="Toggle navigation"
        >
            <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="collapsibleNavId">
            <ul className="navbar-nav ms-auto mt-2 mt-lg-0 ">
                <li className="nav-item ">
                    <NavLink className={'nav-link  text-light font-weight-bold linkh px-3 '} to="About" aria-current="page"
                        >ABOUT
                        <span className="visually-hidden">(current)</span></NavLink>
                </li>
                <li className="nav-item ">
                    <NavLink  className={'nav-link  text-light font-weight-bold linkh px-3 '} to="Portfolio" aria-current="page"
                        >PORTFOLIO
                        <span className="visually-hidden">(current)</span></NavLink>
                </li>
                <li className="nav-item">
                    <NavLink className={'nav-link  text-light font-weight-bold linkh px-3'} to="Contact" aria-current="page"
                        >CONTACT
                        <span className="visually-hidden">(current)</span></NavLink>
                </li>
               
            </ul>
        </div>
    </div>
   </nav>
    </>
  );
}