import React, { useState } from "react";
import Portfoliostyles from "../Portfolio/Portfolio.module.css";
import img1 from "../Portfolio/1.png";
import img2 from "../Portfolio/2.png";
import img3 from "../Portfolio/3.png";

export default function Portfolio() {
  const [showCover, setShowCover] = useState(false);
  const [clickedImageSrc, setClickedImageSrc] = useState("");

  const handleImageClick = (e) => {
    setShowCover(true);
    setClickedImageSrc(e.currentTarget.querySelector("img").src);
    console.log(e.currentTarget.querySelector("img").src);
  };

  return (
    <>
      {showCover && (
        <div
          id="cover"
          className={`vh-100 d-flex ${Portfoliostyles.PhotoCover} fixed-top justify-content-center align-items-center w-100 h-100`}
          onClick={() => setShowCover(false)}
        >
          <div className="w-50 bg-dark">
            <img className="w-100" src={clickedImageSrc} alt="Cover" />
          </div>
        </div>
      )}

      <div className="container pt-5 mt-1 pb-5">
        <div className="d-flex justify-content-center vh-100 ">
          <div className="container">
            <div className="text-center row">
              <div>
                <h1
                  className={`col-md-12 text-uppercase mb-3 fs-1 fw-bolder ${Portfoliostyles.hColor}`}
                >
                  PORTFOLIO COMPONENT
                </h1>
              </div>
              {/* the Lines and stas */}
              <div className="d-flex justify-content-center pb-3">
                <div className={`${Portfoliostyles.line} mt-1`}></div>
                <i
                  className={`fa-solid fa-star px-2 mb-n2 align-self-center text-white star ${Portfoliostyles.hColor}`}
                ></i>
                <div className={`${Portfoliostyles.line} mt-1`}></div>
              </div>
            </div>

            <div className="row gy-5">
              <div
                className="col-12 col-md-6 col-lg-4  "
                onClick={handleImageClick}
              >
                <div className=" img-wraper position-relative overflow-hidden rounded-3  ">
                  <img className="w-100 rounded-3 " src={img1} alt="1" />
                  <div
                    className={` align-items-center justify-content-center  ${Portfoliostyles.fade}  `}
                    src={img1}
                  >
                    <i
                      className={`fa-solid fa-plus w-100 h-50 text-light ${Portfoliostyles.plus}`}
                    ></i>
                  </div>
                </div>
              </div>
              <div
                className="col-12 col-md-6 col-lg-4  "
                onClick={handleImageClick}
              >
                <div className=" img-wraper position-relative overflow-hidden rounded-3  ">
                  <img className="w-100 rounded-3 " src={img2} alt="2" />
                  <div
                    className={` align-items-center justify-content-center  ${Portfoliostyles.fade}  `}
                    src={img1}
                  >
                    <i
                      className={`fa-solid fa-plus w-100 h-50 text-light ${Portfoliostyles.plus}`}
                    ></i>
                  </div>
                </div>
              </div>
              <div
                className="col-12 col-md-6 col-lg-4  "
                onClick={handleImageClick}
              >
                <div className=" img-wraper position-relative overflow-hidden rounded-3  ">
                  <img className="w-100 rounded-3 " src={img3} alt="3" />
                  <div
                    className={` align-items-center justify-content-center  ${Portfoliostyles.fade}  `}
                    src={img1}
                  >
                    <i
                      className={`fa-solid fa-plus w-100 h-50 text-light ${Portfoliostyles.plus}`}
                    ></i>
                  </div>
                </div>
              </div>

              
              <div
                className="col-12 col-md-6 col-lg-4  "
                onClick={handleImageClick}
              >
                <div className=" img-wraper position-relative overflow-hidden rounded-3  ">
                  <img className="w-100 rounded-3 " src={img1} alt="1" />
                  <div
                    className={` align-items-center justify-content-center  ${Portfoliostyles.fade}  `}
                    src={img1}
                  >
                    <i
                      className={`fa-solid fa-plus w-100 h-50 text-light ${Portfoliostyles.plus}`}
                    ></i>
                  </div>
                </div>
              </div>
              <div
                className="col-12 col-md-6 col-lg-4  "
                onClick={handleImageClick}
              >
                <div className=" img-wraper position-relative overflow-hidden rounded-3  ">
                  <img className="w-100 rounded-3 " src={img2} alt="2" />
                  <div
                    className={` align-items-center justify-content-center  ${Portfoliostyles.fade}  `}
                    src={img1}
                  >
                    <i
                      className={`fa-solid fa-plus w-100 h-50 text-light ${Portfoliostyles.plus}`}
                    ></i>
                  </div>
                </div>
              </div>
              <div
                className="col-12 col-md-6 col-lg-4  "
                onClick={handleImageClick}
              >
                <div className=" img-wraper position-relative overflow-hidden rounded-3  ">
                  <img className="w-100 rounded-3 " src={img3} alt="3" />
                  <div
                    className={` align-items-center justify-content-center  ${Portfoliostyles.fade}  `}
                    src={img1}
                  >
                    <i
                      className={`fa-solid fa-plus w-100 h-50 text-light ${Portfoliostyles.plus}`}
                    ></i>
                  </div>
                </div>
              </div>




            </div>
          </div>
        </div>
      </div>
    </>
  );
}
