import React from "react";
import Contactstyles from  "../Contact/Contact.module.css";
import TextField from "@mui/material/TextField";

export default function Contact() {
  return (
    <div className={`container pt-5 mt-1`}>
    <div class={`d-flex justify-content-center vh-100`}>
      <div class={`container`}>
        <div class={`text-center row`}>
          <div>
            <h1 class={`col-md-12 text-uppercase mb-3 fs-1 fw-bolder ${Contactstyles.hColor}`}>CONATCT SECTION</h1>
          </div>
          {/* the Lines and stas */}
          <div class="d-flex justify-content-center pb-3 ">
            <div class={`${Contactstyles.line} mt-1 `}></div>
            <i class={`fa-solid fa-star px-2 mb-n2 align-self-center text-white star ${Contactstyles.hColor}`}></i>
            <div class={`${Contactstyles.line} mt-1 `}></div>
            </div>
            <div className="d-flex flex-column align-items-start pt-3 mt-5">
              <TextField
                className="w-50 m-auto py-3 col col-md-12 "
                id="standard-basic"
                label="userName"
                variant="standard"
              />
              <TextField
                className="w-50 m-auto py-3 col col-md-12"
                id="standard-basic"
                label="userAge"
                variant="standard"
              />
              <TextField
                className="w-50 m-auto py-3 col col-md-12"
                id="standard-basic"
                label="userEmail"
                variant="standard"
              
              />
              <TextField
                className="w-50 m-auto py-3 col col-md-12"
                id="standard-password-input"
                label="userPassword"
                type="password"
                autoComplete="current-password"
                variant="standard"
              />
              <div className="mt-3 w-50 mx-5">
                <button
                  className="btn btn-info text-white ms-4"
                  style={{ backgroundColor: "#1abc9c", border: "none" }}
                >
                  Send Message
                </button>{" "}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
